@import url(https://fonts.googleapis.com/css?family=Pacifico);
@import url(https://fonts.googleapis.com/css?family=Abril+Fatface);
@import url(https://fonts.googleapis.com/css?family=Staatliches);

body {
  margin: 0;
  padding: 0;
  font-family: verdana, Lucida, Lucida;
 
}

.searchContainer {
    background: rgba(244,103,50,1)
}

.searchBox {
    text-align: center;
    padding: 30px 0 0 0;
}

.search {
    text-align: center;
    padding: 15px;
    border-radius: 9px;
    background-color: rgba(0, 0, 0, 0.705);
    border: 1px solid rgba(255, 255, 255, 0.829);
    font-size: large;
    text-transform: capitalize;
    color: #fff;
    transition: all .3s;
}

.search::-webkit-input-placeholder {
    transition: all .3s;
}

.search::-ms-input-placeholder {
    transition: all .3s;
}

.search::placeholder {
    transition: all .3s;
}

.search:focus::-webkit-input-placeholder {
    opacity: 0;
}

.search:focus::-ms-input-placeholder {
    opacity: 0;
}

.search:focus::placeholder {
    opacity: 0;
}
.card {
    height: 30vh;
    width: 100vw;
    background-color: rgb(221, 98, 0);
    margin: 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-hyphens: auto;
        -ms-hyphens: auto;
            hyphens: auto;
    transition: all 1s;
    font-size: 1.2em;
    text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
}

.card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 0.8;
    transition: all 1s;
}

@media screen and (min-width: 480px) {
    .card {
        height: 26vh;
        width: 40vw;
        border-radius: 10px;
        font-size: 1.5em;
    }
}

@media screen and (min-width: 720px) {
    .card {
        height: 25vw;
        width: 25vw;
    }
}

@media screen and (min-width: 720px) {
    .card {
        height: 25vw;
        width: 25vw;
    }
}

@media screen and (min-width: 920px) {
    .card {
        height: 15vw;
        width: 15vw;
    }
}
h2 {
    font-size: 2em;
}

.region {
    /* width: 300px; */
    width: 100%;
}

.region-card {
    width: 400px;
    height: 400px;
    list-style: none;
    /* vertical-align: top; */
}

.region-card h3 {
    cursor: pointer;
}

.regiontext{
    font-family: 'Pacifico', cursive;
    font-size: 1rem;

}
.list{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    padding: 0;
    margin: 1.1em;
}

.header {
    background-image: linear-gradient(rgba(199, 9, 176, 0.5), rgba(30, 203, 255, 0.9)), url(https://images.unsplash.com/photo-1503457574462-bd27054394c1?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80);
    /* background-attachment: fixed; */
    background-position: 40% 80%;
    background-repeat: no-repeat;
    background-size: cover;
    mix-blend-mode: saturation;
}

.headertext {
    background: #fff;
    letter-spacing: 7px;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Staatliches', cursive;
    font-size: 6em;
    text-transform: uppercase;
}

.nav {
    background-color: rgba(0, 0, 0, 0.705);
    position: relative;
    bottom: -35px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

